.container {
  position: relative;
  width: $containerSize;
  height: $containerSize;
  margin: auto;

  &__minute {
    position: absolute;
    left: calc(#{$firstPosition} + 2px);
    top: 225px;
    border-radius: 50%;
    width: 6px;
    height: 80px;
    background-color: $colorMin;

    transform-origin: bottom;

    animation: clock-hand-rotate 3600s linear infinite;
  }

  &__second {
    position: absolute;
    left: calc(#{$firstPosition} + 2px);
    top: 205px;
    border-radius: 50%;
    width: 4px;
    height: 100px;
    background-color: $colorSec;

    transform-origin: bottom;

    animation: clock-hand-rotate 60s linear infinite;
  }

  &__center {
    position: absolute;
    left: $firstPosition;
    top: $firstPosition;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background-color: $colorCenter;
    opacity: 0.3;
  }
}

@keyframes clock-hand-rotate {
  from {
      transform: rotate(0deg)
  }
  to {
      transform: rotate(360deg)
  }
}
